











import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Btn extends Vue {
    @Prop()
    readonly color!: 'default' | 'primary' | 'danger'

    @Prop({
        default: 'button',
    })
    readonly type!: 'button' | 'submit'

    onClick(e: any) {
        this.$emit('click', e)
    }
}
